
.menu {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  color: #707070;
  background-color: #fff;
  transition:all .618s;
  -webkit-transition:all .618s;
  -moz-transition:all .618s;
}
.menu:before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.menu-item.active {
  color:  #e35160;
}
.product-detail + .menu{
  opacity: 0;
  bottom: -4.5rem;
}
.menu-item {
  display: inline-block;
  width: 50%;
  text-align: center;
  height: 4rem;
  font-size: 1.6rem;
  line-height: 4rem;
  letter-spacing: 0.5rem;
}



