
.slide-img {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.swiper-container {
  width: 100%;
  height: 15rem;
}
.swiper-pagination-bullet-active {
  background-color: #e35160;
}
.swiper-pagination-bullet {
  width: .8rem !important;
  height: .8rem !important;
  margin-right: .5rem !important;
  margin-left: .5rem !important;
}
.classify {
  width: 100%;
  position: relative;
}
.classify .active .name,.classify .active .count{
  color: #e35160 !important;
}
.classify .active:before{
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 11;
  width: 100%;
  height: .2rem;
  background-color: #e35160 ;
}
.classify:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.classify-item {
  width: calc(100% / 3);
  display: inline-block;
  text-align: center;
  padding: 0.5rem;
  position: relative;
}
.classify-item:after{
  content: '';
  position: absolute;
  left: 0;
  top:25%;
  z-index: 11;
  width: 0.1rem;
  opacity: 0.5;
  height: 50%;
  background-color: #acacac;
}
.classify-item:first-of-type:after{
  content: '';
  width: 0;
  height: 0;
}
.classify-item:active,.filter-item:active{
  background-color:#efefef ;
}
.classify-item .count {
  display: block;
  font-size: 1.8rem;
}
.classify-item .name {
  display: block;
  font-size: 1.2rem;
  color: #a9a9a9;
}
.filter{
  width: 100%;
  position: relative;
}
.filter:after{
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.filter .active{
  color: #e35160;
}
.filter-item{
  width:calc(100%/4);
  display: inline-block;
  text-align: center;
  font-size: 1.5rem;
  padding: 0.8rem;
}
.filter .sort{
  display: inline-block;
  width: 1rem;
}
.product{
  background-color:#e8e8e8;
  width: 100%;
  position: relative;
  /*overflow: hidden;清除浮动方法*/
}
/*清除浮动*/
.product:after{
  content:".";
  display:block;
  height:0;
  clear:both;
  visibility:hidden;
}
.product-item{
  width: calc(50% - .1rem);
  display: inline-block;
  margin:0 .1rem .1rem 0;
  float: left;
  background-color: #ffffff;
  text-align: left;
}
.product-item .img{
  width:100%;
  height: 16rem;
  overflow: hidden;
}
.product-item .img img{
  width: 100%;
}
.product-item .description{
  width: 100%;
  padding:1rem 1rem 0 ;
  font-size: 1.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.product-item .price{
  display: inline-block;
  width: 100%;
  background-color: #ffffff;
  font-size: 2rem;
  color: #e35160 ;
  padding:0.5rem 1rem 1rem;
  font-weight:500;
}
.product-item:active .price,.product-item:active  .description{
  background-color:#efefef ;
}
.product-item .price .money-icon{
  font-size: 1rem;
}



