
.product-detail {
  background-color: #e8e8e8;
  font-size: 0;
  text-align: left;
  padding-bottom: 5.6rem;
  min-height: 100%;
}
.item {
  color: #a9a9a9;
}
.product-display {
  background-color: #ffffff;
  width: 100%;
  margin-bottom: 1rem;
}
.product-display .img {
  width: 100%;
}
.product-swiper {
  height: 30rem !important;
}
.back-before {
  display: inline-block;
  text-align: center;
  width: 3.6rem;
  height: 3.6rem;
  font-size: 3rem;
  color: #ffffff;
  position: absolute;
  z-index: 4;
  top: 1.6rem;
  left: 1rem;
  background-color: #000000;
  border-radius: 50%;
  line-height: 3.6rem;
  opacity: 0.5;
  text-indent: -0.3rem;
}
.add-cart {
  display: inline-block;
  text-align: center;
  width: 3.6rem;
  height: 3.6rem;
  font-size: 2rem;
  color: #ffffff;
  position: absolute;
  z-index: 4;
  top: 1.6rem;
  right: 1rem;
  background-color: #000000;
  border-radius: 50%;
  line-height: 3.6rem;
  opacity: 0.5;
  text-indent: -0.3rem;
}
.add-cart-animate {
  width: 0;
  height: 0;
  position: absolute;
  top: 1.6rem;
  right: 1rem;
}
.add-cart-animate img {
  width: 100%;
  height: 100%;
}
.adding {
  -webkit-animation: add-cart 1s;
          animation: add-cart 1s;
}
.product-display .description {
  position: relative;
  width: 100%;
  font-size: 1.6rem;
  padding: 0.5rem 1rem 0;
}
.product-display .description:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.product-display .price {
  position: relative;
  width: 100%;
  font-size: 2.2rem;
  color: #e35160;
  font-weight: bold;
  padding: 0.5rem 1rem 1rem;
}
.product-display .price:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.product-display .price span {
  font-size: 1.4rem;
}
.choose-count {
  background-color: #ffffff;
  font-size: 1.4rem;
  padding: 1rem 1rem;
  overflow: hidden;
  position: relative;
  margin-bottom: 1rem;
}
.choose-count:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.choose-count:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.choose-count .version-selected {
  padding-left: .5rem;
}
.choose-count .count-selected {
  padding-left: .5rem;
}
.version {
  overflow: hidden;
  margin-top: .5rem;
}
.version .item {
  display: inline-block;
  float: left;
  padding-top: 1rem;
}
.version .version-btn-wrapper {
  width: calc(100% - 2.8rem);
  display: inline-block;
}
.version .version-btn-wrapper .active {
  color: #e35160;
  border: .05rem solid #e35160;
}
.version-btn {
  display: inline-block;
  width: calc(50% - 2rem);
  text-align: center;
  padding: .5rem;
  margin: .5rem 1rem .5rem;
  border: 0.05rem solid #707070;
  border-radius: .5rem;
}
.version-btn:active{
  background-color: #f0f0f0;
}
.count {
  font-size: 0;
  margin-top: 1rem;
}
.count .item {
  float: left;
  font-size: 1.4rem;
  line-height: 3rem;
}
.count-btn-wrapper {
  float: left;
  position: relative;
  margin-left: 1rem;
  border: 0.05rem solid #707070;
}
.count .reduce, .count .plus {
  display: inline-block;
  font-size: 2rem;
  text-align: center;
  line-height: 3rem;
  width: 3rem;
  height: 3rem;
  background-color: #f0f0f0;
}
.count .reduce:active, .count .plus:active {
  background-color: #cbcbcb;
}
.count .count-input {
  width: 4rem;
  height: 3rem;
  border: none;
  font-size: 2rem;
  line-height: 3rem;
  text-align: center;
  position: absolute;
  left: 3rem;
  top: 0
}
.count .count-input:focus {
  outline: none;
}
.count .reduce {
  width: 1.5rem !important;
  transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  transform: scaleX(2);
  -webkit-transform: scaleX(2);
}
.count .plus {
  margin-left: 5.5rem;
}
.count-error {
  font-size: 1.4rem;
  line-height: 3rem;
  color: #e35160;
  padding-left: 1rem;
}
.choose-address {
  font-size: 1.4rem;
  background-color: #ffffff;
  position: relative;
}
.choose-address:before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.choose-address:after{
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #acacac;
  transform: translateY(0.5);
  -webkit-transform: translateY(0.5);
}
.address-wrapper {
  padding:0 0 0 1rem;
  overflow: hidden;
  height: 4.6rem;
  line-height: 4.6rem;
}
.address-wrapper:active{
  background-color:#f0f0f0;
}
.address-wrapper .item {
  display: inline-block;
  float: left;
}
.address {
  display: inline-block;
  width: calc(100% - 3.8rem);
  padding: 0 5rem 0 2rem;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.address-icon {
  color: #e35160;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  transform: scale(1.2) translateX(-0.45rem);
  -webkit-transform: scale(1.2) translateX(-0.45rem);
}
.address-choose-icon {
  position: absolute;
  font-size: 2rem;
  line-height: 4.6rem;
  color: #9c9c9c;
  top: 0;
  right: 2rem;
}
.notice-wrapper{
  padding: 1rem;
}
.notice-wrapper .notice{
  color: #a9a9a9;
  padding-left: 1rem;
}
.buy {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: .6rem 2rem;
  background-color: #e8e8e8;
}
.buy-btn {
  width: 100%;
  height: 4.2rem;
  text-align: center;
  font-size: 1.6rem;
  line-height: 4.2rem;
  color: #ffffff;
  background-color: #e35160;
  border-radius: .5rem;
}
.buy-btn:active {
  opacity: 0.618;
}
@-webkit-keyframes add-cart {
0% {
    width: 100%;
    height: 30rem;
    z-index: 2;
    opacity: 0;
}
30% {
    width: 50%;
    height: 15rem;
    z-index: 5;
    top: 10rem;
    right: 10%;
    opacity: 0.5;
}
100% {
    width: 0;
    height: 0;
    z-index: 5;
    top: 1.6rem;
    right: 2.6rem;
    opacity: 1
}
}
@keyframes add-cart {
0% {
    width: 100%;
    height: 30rem;
    z-index: 2;
    opacity: 0;
}
30% {
    width: 50%;
    height: 15rem;
    z-index: 5;
    top: 10rem;
    right: 10%;
    opacity: 0.5;
}
100% {
    width: 0;
    height: 0;
    z-index: 5;
    top: 1.6rem;
    right: 2.6rem;
    opacity: 1
}
}



